'use strict';
import {COUPON_BG} from '@/config/image.js'
var _self
export default {
	data() {
		return{
			img:[COUPON_BG],
			list:[]
		}
	},
	methods:{
		getList(e){
			_self.list=e.map(res=>{
				let num= res.describe.replace(/[^0-9]/ig,"")
				return{
					...res,
					num:num
				}
			})
		}
	},
	 onLoad(options) {
		 _self=this
		this.getList(JSON.parse(options.data))
	}
};
